import {
  type Subject,
  distinctUntilChanged,
  Observable,
  BehaviorSubject,
  of,
  map,
  shareReplay,
  combineLatestWith,
  withLatestFrom,
  startWith,
  auditTime,
  fromEvent,
  firstValueFrom,
  pairwise,
  skip,
  merge,
  scan
} from "rxjs";
import confetti from "canvas-confetti";

import { msciWorldReturns } from "./datasets";
import './styles.css'
import { addOverlay } from "./addOverlay";

const YEARS = 10;
const BIN_WIDTH = 1000;
const INITIAL_CAPITAL = 10000;
const SAFE_RETURN = INITIAL_CAPITAL * Math.pow(1 + 0.02, YEARS);

export type yAxisConfig = 'draws' | 'bins' | 'fixed';

declare global {
  interface Window {
    // Input variables
    risky_share_start: number | undefined; // from 0.1 to 1.0
    draws: number | undefined;  // from 0 to 1000
    y_axis_config: yAxisConfig | undefined;
    y_axis_fixed_count: number | undefined; // from 0 to 1000
  
    // Output variables
    clickstream: string;
    risky_share_end: number;
  }
}

window.clickstream = "";
window.risky_share_end = Math.max(0, Math.min(1.0, window.risky_share_start ?? 0.5));

// Build observable for our drawn returns
//const state$ = new BehaviorSubject<'initial' | 'drawReturn' | 'modifyDistribution'>('initial')
const nDraws$ = new BehaviorSubject<number>(Math.max(0, Math.min(1000, window.draws ?? 0)));
const shareOfRiskyAssets$ = new BehaviorSubject<number>(Math.max(0, Math.min(1.0, window.risky_share_start ?? 0.5)));
const state$ = nDraws$
.pipe(
  map(n => {
    if (n === 0) return 'initial';
    if (n < 1000) return 'drawReturn';
    return 'modifyDistribution';
  }),
  distinctUntilChanged()
);


const returns$ = nDraws$.pipe(
  combineLatestWith(shareOfRiskyAssets$),
  map(([nDraws, shareOfRiskyAssets]) =>
    msciWorldReturns
      .slice(0, nDraws)
      .map(
        (risky) =>
          shareOfRiskyAssets * risky + (1 - shareOfRiskyAssets) * SAFE_RETURN
      )
  )
);

// Latest return
const latestReturn$ = returns$.pipe(
  map((returns) => returns[returns.length - 1] ?? INITIAL_CAPITAL),
  distinctUntilChanged(),
  shareReplay(1)
);

// Bin the returns
const bins = new Map<number, Observable<number>>();
for (let binStart = 0; binStart <= 51000; binStart += BIN_WIDTH) {
  const bin$ = returns$.pipe(
    map(
      (returns) =>
        returns.filter((r) => r >= binStart && r < binStart + BIN_WIDTH).length
    ),
    startWith(0),
    distinctUntilChanged(),
    shareReplay(1)
  );
  bins.set(binStart, bin$);
}

// Get the max count across all bins
const maxCount$ = of(0).pipe(
  combineLatestWith(...bins.values()),
  map((values) => Math.max(...values)),
  distinctUntilChanged(),
  auditTime(0),
  shareReplay(1)
);

const fixedCount$ = new BehaviorSubject(Math.max(0, Math.min(window.y_axis_fixed_count ?? 25)));
const yAxisIsDynamic$ = new BehaviorSubject(true);

if (typeof window.y_axis_config === 'string') {
  if (!['draws', 'bins', 'fixed'].includes(window.y_axis_config)) {
    console.error(`Invalid y_axis_config: ${window.y_axis_config}, will use default 'draws'`);
    window.y_axis_config =  'draws';
  }
}
const yAxisConfig$ = new BehaviorSubject<yAxisConfig>(window.y_axis_config ?? 'draws');



fixedCount$.subscribe((value) => {
  console.debug("fixedCount", value);
})

yAxisIsDynamic$.subscribe((value) => {
  console.debug("yAxisIsDynamic", value);
})

maxCount$.subscribe((value) => {
  console.debug("maxCount", value);
})

const scaleOfYAxisCount$ = yAxisConfig$.pipe(
  combineLatestWith(maxCount$, fixedCount$, nDraws$),
  map(([yAxisConfig, maxCount, fixedCount, nDraws]) => { 
    return yAxisConfig === 'draws' 
      ? Math.round(25 + nDraws * 0.275)
      : yAxisConfig === 'bins' 
      ? Math.max(25, Math.min(300, maxCount)) 
      : Math.max(25, Math.min(300, fixedCount));
  }),
  startWith(25),
  distinctUntilChanged(),
  shareReplay(1)
);

scaleOfYAxisCount$
  .pipe(
    withLatestFrom(yAxisConfig$),
    distinctUntilChanged()
  )
  .subscribe(([value, yAxisConfig]) => {
    if (yAxisConfig !== 'fixed') {
      fixedCount$.next(value);
    }
  })

const scaleOfYAxis$ = scaleOfYAxisCount$.pipe(
  map((count) => 300 / count),
  startWith(1),
  distinctUntilChanged(),
  shareReplay(1)
);

// Get the average return
const averageReturn$ = returns$.pipe(
  map((returns) => returns.reduce((a, b) => a + b, 0) / returns.length),
  distinctUntilChanged(),
  shareReplay(1)
);

averageReturn$.subscribe((value) => {
  console.debug("averageReturn", value);
});

// Compute percentiles
const sortedReturns$ = returns$.pipe(
  map((returns) => [...returns].sort((a, b) => a - b)),
  shareReplay(1)
);

const percentile10$ = sortedReturns$.pipe(
  map((returns) => {
    const below = Math.floor(returns.length * 0.1);
    const above = Math.ceil(returns.length * 0.1);
    return (returns[below] + returns[above]) / 2;
  }),
  distinctUntilChanged(),
  shareReplay(1)
);

percentile10$.subscribe((value) => {
  console.debug("percentile10", value);
});

const percentile90$ = sortedReturns$.pipe(
  map((returns) => {
    const below = Math.floor(returns.length * 0.9);
    const above = Math.ceil(returns.length * 0.9);
    return (returns[below] + returns[above]) / 2;
  }),
  distinctUntilChanged(),
  shareReplay(1)
);

function assertSvgElement(element: Element): asserts element is SVGElement {
  // We cannot check for `instanceof SVGElement` because inside <object> elements, we will have to different SVGElement type hierarchies
  if (
    !element ||
    element.namespaceURI !== "http://www.w3.org/2000/svg" || 
    !('getBBox' in element)
  ) throw new Error(`Element is not an SVGElement.`);
}

function getSVGElementById(id: string): SVGElement {
  let element = document.getElementById(id);
  if (element === null) {
    const svgAsObject = document.getElementById("svg-as-object") as HTMLObjectElement
    if (svgAsObject?.contentDocument === null) 
      throw new Error(`svg as object not found`);
    element = svgAsObject.contentDocument.getElementById(id);
  }
  if (element === null) {
    throw new Error(`SVG element with id ${id} not found`);
  }
  assertSvgElement(element);
  return element
}

function getHTMLElementById(id: string): HTMLElement | null {
  let element = document.getElementById(id);
  if (element === null) {
    console.error(`HTML element with id ${id} not found`);
  }
  return element
}

function syncRangeInputWithSubject(input: HTMLInputElement, subject: Subject<number>, { factor = 1.0, output }: { factor?: number, output?: HTMLOutputElement }) {
  fromEvent(input, "input")
    .pipe(
      map(event => parseInt((event.target as HTMLInputElement).value) / factor),
      distinctUntilChanged()
    )
    .subscribe(nextShare => {
      subject.next(nextShare);
    })
  
  subject
    .pipe(distinctUntilChanged())
    .subscribe(nextShare => {
      input.value = (nextShare * factor).toFixed(0);
      if (output) {
        output.value = (nextShare * factor).toFixed(0);
      }
    })
}

window.onload = async function initialize() {
  // Is embedded
  const svgObject = document.getElementById("svg-as-object") as HTMLObjectElement;
  if (svgObject) {
    if (svgObject.contentDocument === null)
      await firstValueFrom(fromEvent(svgObject, "load"))
  }

  // Sync draws with input
  const drawsInput = getHTMLElementById("draws-input") as HTMLInputElement | null;
  const drawsOutput = getHTMLElementById("draws-output") as HTMLOutputElement | null;
  if (drawsInput && drawsOutput)
    syncRangeInputWithSubject(drawsInput, nDraws$, { factor: 1, output: drawsOutput });
  
  // Sync risk with input
  const riskInput = getHTMLElementById("risk-input") as HTMLInputElement | null;
  const riskOutput = getHTMLElementById("risk-output") as HTMLOutputElement | null;
  if (riskInput && riskOutput)
    syncRangeInputWithSubject(riskInput, shareOfRiskyAssets$, { factor: 100, output: riskOutput });

  // Sync fixed count with input
  const yCountInput = getHTMLElementById("y-count-input") as HTMLInputElement | null;
  const yCountOutput = getHTMLElementById("y-count-output") as HTMLOutputElement | null;
  if (yCountInput && yCountOutput)
    syncRangeInputWithSubject(yCountInput, fixedCount$, { factor: 1, output: yCountOutput });

  // Sync checkox with subject
  const radios = document.getElementsByName("y-scale") as NodeListOf<HTMLInputElement>
  fromEvent(radios, "change")
    .pipe(
      map(event => {
        const value = (event.target as HTMLInputElement).value;
        console.debug('value', value);
        switch (value)  {
          case "draws": 
          case "bins":
          case "fixed": return value;
          default: throw new Error(`Unknown value: ${value}`);
        }
      }),
      distinctUntilChanged()
    ).subscribe(nextValue => {
      yAxisConfig$.next(nextValue);
    })
  
  if (yCountInput) {
    yAxisConfig$
      .pipe(distinctUntilChanged())
      .subscribe(value => {
        yCountInput.disabled = value !== "fixed";
        radios.forEach(radio => {
          radio.checked = radio.value === value;
        })
      })
  }

  // Get SVG elements for use in script
  const svgRoot = getSVGElementById("svg1");
  const oneSimulationButton = getSVGElementById("run-one-simulation-button");
  const remainingSimulationsButton = getSVGElementById("remaining-simulations-button");
  const remainingSimulationsButtonBg = getSVGElementById("remaining-simulations-button__bg");
  const plusButton = getSVGElementById("plus-button");
  const minusButton = getSVGElementById("minus-button");
  const finalizeButton = getSVGElementById("finalize-button");
  const plotArea = getSVGElementById("plot-area") as SVGGElement;
  const plotAreaClip = getSVGElementById("plot-area__clip-path-rect") as SVGRectElement;
  const initialAssets = getSVGElementById("initial-assets") as SVGRectElement;
  const finalAssets = getSVGElementById("final-assets") as SVGRectElement;
  const finalAssetsLabel = getSVGElementById("final-assets__label") as SVGTextElement;
  const yAxisTick25 = getSVGElementById("y-axis__tick-25") as SVGGElement;
  const yAxisTick50 = getSVGElementById("y-axis__tick-50") as SVGGElement;
  const yAxisTick100 = getSVGElementById("y-axis__tick-100") as SVGGElement;
  const yAxisTick200 = getSVGElementById("y-axis__tick-200") as SVGGElement;
  const yAxisTick300 = getSVGElementById("y-axis__tick-300") as SVGGElement;
  
  const okInitialAssets$ = new BehaviorSubject(false);
  addOverlay({ 
    target: initialAssets, 
    text: 'Sie starten mit dieser Geld-Anlage.',
    button: {
      text: 'OK',
      onClick: () => okInitialAssets$.next(true)
    },
    hideIf: nDraws$.pipe(
      combineLatestWith(okInitialAssets$), 
      map(([n, ok]) => n > 0 || ok)
    )    });

  addOverlay({ 
    target: oneSimulationButton, 
    text: 'Simulieren Sie 10 Jahre Wert-Entwicklung!', 
    placement: 'left',
    hideIf: nDraws$.pipe(
      combineLatestWith(okInitialAssets$), 
      map(([n, ok]) => n > 0 || !ok)
    )  
  });
  
  const okFirstSimulation$ = new BehaviorSubject(false);
  addOverlay({ 
    target: finalAssets, 
    text: 'Anlage-Wert nach 10 Jahren.',
    button: {
      text: 'OK',
      onClick: () => okFirstSimulation$.next(true)
    },
    hideIf: nDraws$.pipe(combineLatestWith(okFirstSimulation$), map(([n, ok]) => n !== 1 || ok))
  });
  
  addOverlay({ 
    target: finalAssets, 
    text: 'Ergebnis der 2. Simulation.',
    hideIf: nDraws$.pipe(map(nDraws => nDraws !== 2))
  });
  
  addOverlay({ 
    target: finalAssets, 
    text: 'Ergebnis der 3. Simulation.',
    hideIf: nDraws$.pipe(map(nDraws => nDraws !== 3))
  });

  addOverlay({ 
    target: plotArea, 
    placement: 'bottom',
    text: 'So verteilen sich die Simulations-Ergebnisse.',
    hideIf: nDraws$.pipe(map(nDraws => nDraws < 4 || nDraws >= 1000))
  });
  
  addOverlay({ 
    target: finalAssets, 
    text: 'Ergebnis der 4. Simulation.',
    hideIf: nDraws$.pipe(map(nDraws => nDraws !== 4))
  });
  
  addOverlay({ 
    target: oneSimulationButton, 
    text: nDraws$.pipe(map(nDraws => `Erneut simulieren (${nDraws}/10)`)),
    placement: 'left',
    styles: nDraws$.pipe(map(nDraws => ({ opacity: Math.max(0, 1 - (Math.max(0, nDraws-10) / 4.)).toFixed(2) }))),
    hideIf: nDraws$.pipe(
      combineLatestWith(okFirstSimulation$),
      map(([n, ok]) => n < 1 || n > 14 || (!ok && n < 2))
    )
  });
  
  addOverlay({ 
    target: remainingSimulationsButton, 
    text: '1000x simulieren',
    placement: 'right',
    fallbackPlacements: ['top', 'bottom'],
    hideIf: nDraws$.pipe(map(nDraws => nDraws < 10 || nDraws >= 1000))
  });
  
  const captionOf10thPercentile = getSVGElementById("10th-percentile__caption") as SVGGElement;
  const groupOf10thPercentile = getSVGElementById("10th-percentile") as SVGGElement;
  const valueOf10thPercentile = getSVGElementById("10th-percentile__value") as SVGTextElement;

  const captionOf90thPercentile = getSVGElementById("90th-percentile__caption") as SVGGElement;
  const groupOf90thPercentile = getSVGElementById("90th-percentile") as SVGGElement;
  const valueOf90thPercentile = getSVGElementById("90th-percentile__value") as SVGTextElement;
  
  const groupOfAvgReturn = getSVGElementById("avg") as SVGGElement;
  const valueOfAvgReturn = getSVGElementById("avg__value") as SVGTextElement;

  const explainedPercentile$ = new BehaviorSubject<'10' | 'avg' | '90' | 'done'>('avg');
  const mergedRiskButtons$ = merge(
    fromEvent(plusButton, "click").pipe(map(() => '+')),
    fromEvent(minusButton, "click").pipe(map(() => '-')),
  );
  const clickedRiskButton$ = mergedRiskButtons$.pipe(
    scan((count) => count + 1, 0),
    startWith(0),
    shareReplay(1)
  );
  clickedRiskButton$.subscribe((n) => { console.log('clickedRiskButton', n) });

  // Define global output variables
  mergedRiskButtons$.forEach((sign) => {
    window.clickstream += sign;
  });

  shareOfRiskyAssets$.forEach((value) => {
    window.risky_share_end = value;
  });  

  addOverlay({ 
    target: valueOfAvgReturn, 
    text: 'Durchschnittliche Entwicklung nach dieser Simulation.',
    placement: 'bottom',
    styles: {
      width: '16em',
      textAlign: 'center'
    },
    button: {
      text: 'weiter',
      onClick: () => explainedPercentile$.next('10')
    },
    hideIf: nDraws$.pipe(
      combineLatestWith(explainedPercentile$, clickedRiskButton$),
      map(([n, explained, clicked]) => n < 1000 || explained !== 'avg' || clicked > 0)
    )
  });

  addOverlay({ 
    target: valueOf10thPercentile, 
    text: 'Die niedrigsten 100 Ergebnisse der Ziehung liegen unter diesem Wert.',
    placement: 'bottom',
    styles: {
      width: '18em',
      textAlign: 'center'
    },
    button: {
      text: 'weiter',
      onClick: () => explainedPercentile$.next('90')
    },
    hideIf: nDraws$.pipe(
      combineLatestWith(explainedPercentile$, clickedRiskButton$),
      map(([n, explained, clicked]) => n < 1000 || explained !== '10' || clicked > 0)
    )
  });

  addOverlay({ 
    target: valueOf90thPercentile, 
    text: 'Die hchsten 100 Ergebnisse der Ziehung liegen ber diesem Wert.',
    placement: 'bottom',
    styles: {
      width: '18em',
      textAlign: 'center'
    },
    button: {
      text: 'ok',
      onClick: () => explainedPercentile$.next('done')
    },
    hideIf: nDraws$.pipe(
      combineLatestWith(explainedPercentile$, clickedRiskButton$),
      map(([n, explained, clicked]) => n < 1000 || explained !== '90' || clicked > 0)
    )
  });

  addOverlay({ 
    target: plusButton, 
    text: 'Mehr in den ETF',
    placement: 'bottom',
    styles: clickedRiskButton$.pipe(map(n => ({ opacity: Math.max(0, 1 - (Math.max(0, n) / 4.)).toFixed(2) }))),
    hideIf: nDraws$.pipe(
      combineLatestWith(clickedRiskButton$, explainedPercentile$),
      map(([n, clicked, explained]) => n < 1000 || clicked > 4 || explained !== 'done')
    )
  });

  addOverlay({ 
    target: minusButton, 
    text: 'Mehr aufs Sparkonto',
    placement: 'top',
    styles: clickedRiskButton$.pipe(map(n => ({ opacity: Math.max(0, 1 - (Math.max(0, n) / 4.)).toFixed(2) }))),
    hideIf: nDraws$.pipe(
      combineLatestWith(clickedRiskButton$, explainedPercentile$),
      map(([n, clicked, explained]) => n < 1000 || clicked > 4 || explained !== 'done')
    )
  });

  averageReturn$.subscribe((value) => {
    valueOfAvgReturn.textContent = value.toLocaleString('de-DE', { style: 'currency', currency: 'EUR', currencyDisplay: 'symbol', roundingMode: 'trunc', maximumFractionDigits: 0 });
    groupOfAvgReturn.style.transform = `translate(${value * 0.02}px, 0)`;
  })

  fromEvent(groupOf10thPercentile, "transitionend")
    .pipe(combineLatestWith(percentile10$))
    .subscribe(() => {
      if (groupOf10thPercentile.getBoundingClientRect().right >= groupOfAvgReturn.getBoundingClientRect().left) {
        captionOf10thPercentile.style.transform = `translate(-392px, 40px)`;
      } else {
        captionOf10thPercentile.style.transform = `translate(-392px, 0)`;
      }
  })

  fromEvent(groupOf90thPercentile, "transitionend")
    .pipe(combineLatestWith(percentile90$))
    .subscribe(() => {
      if (groupOf90thPercentile.getBoundingClientRect().left <= groupOfAvgReturn.getBoundingClientRect().right) {
        captionOf90thPercentile.style.transform = `translate(-664px, 40px)`;
      } else {
        captionOf90thPercentile.style.transform = `translate(-664px, 0)`;
      }
  })

  percentile10$.subscribe((value) => {
    valueOf10thPercentile.textContent = value.toLocaleString('de-DE', { style: 'currency', currency: 'EUR', currencyDisplay: 'symbol', roundingMode: 'trunc', maximumFractionDigits: 0 });
    groupOf10thPercentile.style.transform = `translate(${value * 0.02}px, 0)`;
  })

  percentile90$.subscribe((value) => {
    valueOf90thPercentile.textContent = value.toLocaleString('de-DE', { style: 'currency', currency: 'EUR', currencyDisplay: 'symbol', roundingMode: 'trunc', maximumFractionDigits: 0 });
    groupOf90thPercentile.style.transform = `translate(${value * 0.02}px, 0)`;
  })

  // Add the current state as a class to the SVG root
  state$.pipe(
    startWith('initial' as 'initial'),
    pairwise()
  ).subscribe(([oldState, newState]) => {
    if (!svgRoot.classList.replace(oldState, newState)) {
      svgRoot.classList.add(newState);
    }
  })

  // Prepare the histogram
  bins.forEach((bin$, binStart) => {
    // create bar
    const bar = document.createElementNS("http://www.w3.org/2000/svg", "rect");
    bar.setAttribute("x", binStart.toString());
    bar.setAttribute("y", "0");
    bar.setAttribute("width", "1000");
    bar.style.height = "0";
    bar.id = "bar-" + binStart;
    bar.classList.add("bar");
    plotArea.appendChild(bar);

    // subscribe to changes
    bin$.subscribe((height) => {
      console.debug("update bin", binStart, height);
      bar.style.height = `${height}px`;
    });
  });

  // Animate the final assets bar
  latestReturn$
    .subscribe((value) => {
      finalAssets.style.width = (value * 0.02).toFixed(0) + "px";
      finalAssetsLabel.textContent = value.toLocaleString('de-DE', { style: 'currency', currency: 'EUR', currencyDisplay: 'symbol', roundingMode: 'trunc', maximumFractionDigits: 0 });
      finalAssetsLabel.style.transform = `translate(${(value * 0.02 + 352 - 10000 * 0.02).toFixed(0)}px, 0)`
    });
    
  latestReturn$
    .pipe(
      withLatestFrom(state$),
      skip(1)
    )
    .subscribe(([value, _]) => {
      if (_ === 'modifyDistribution') return;

      // create drop
      const drop = document.createElementNS("http://www.w3.org/2000/svg", "rect");
      const binStart = Math.floor(value / 1000) * 1000;
      drop.setAttribute("x", (160 + binStart * 0.02).toFixed(0));
      drop.setAttribute("y", "160")
      drop.setAttribute("width", "20");
      drop.style.fill = "#4dabf7";
      drop.style.height = "20px";
      drop.style.position = "relative"
      drop.style.top = "0";
      drop.style.opacity = "0";

      // append drop
      svgRoot.appendChild(drop);

      // animate drop
      const animation = drop.animate(
        [
          { transform: 'translate(0, 0)', opacity: 1, height: "15px" },
          { transform: 'translate(0, 205px)', opacity: 0.7, height: "25px" },
          { transform: 'translate(0, 414px)', opacity: 0.4, height: "5px" },
        ],
        {
          duration: 400,
          delay: 500,
          fill: "forwards",
        }
      )

      animation.onfinish = () => {
        svgRoot.removeChild(drop);
      }
    })

  scaleOfYAxis$.subscribe((scale) => {
    yAxisTick25.style.transform = `translate(0, ${-scale*25}px)`
    yAxisTick50.style.transform = `translate(0, ${-scale*50}px)`
    yAxisTick100.style.transform = `translate(0, ${-scale*100}px)`
    yAxisTick200.style.transform = `translate(0, ${-scale*200}px)`
    yAxisTick300.style.transform = `translate(0, ${-scale*300}px)`
    plotArea.style.transform = `translate(160px, 580px) scale(0.02, -${scale})` //`matrix(0.02, 0, 0, -${scale}, 160, 580)`
    plotAreaClip.style.transform = `scale(1, ${-1 / Math.max(scale, 1)})`
  })

  fromEvent(plusButton, "click").subscribe(() => {
    shareOfRiskyAssets$.next(Math.min(1, shareOfRiskyAssets$.getValue() + 0.05));
  });

  fromEvent(minusButton, "click").subscribe(() => {
    shareOfRiskyAssets$.next(Math.max(0, shareOfRiskyAssets$.getValue() - 0.05));
  });

  const oneSimulationClick$ = fromEvent(oneSimulationButton, "click");

  nDraws$.subscribe((nDraws) => {
    remainingSimulationsButtonBg.style.fill = nDraws >= 10 ? "#ffd43b" : "#f3de95"; 
  })

  // Change the final assets when the button is clicked
  oneSimulationClick$
    .pipe(withLatestFrom(nDraws$))
    .subscribe(([_, nDraws]) => { 
      nDraws$.next(Math.min(1000, nDraws + 1));
    })
  
  fromEvent(remainingSimulationsButton, "click")
    .subscribe(() => { 
      const offset = 10
      const stepWidth = 1;
      if (nDraws$.getValue() < offset) return;
      if (nDraws$.getValue() < 1000) {
        function stepValue() {
          const currentValue = nDraws$.getValue();
          const nextValue = Math.min(1000, currentValue + stepWidth);
          console.debug('nextValue', nextValue);
          nDraws$.next(nextValue);
          if (nextValue < 1000) {
            const baseDelay = 12; // minimum delay in ms
            const startDelay = 900; // starting delay in ms
            const decayRate = 0.15; // controls how quickly delay decreases
            const delay = baseDelay + (startDelay - baseDelay) * Math.exp(-decayRate * (nextValue - offset - stepWidth));
            finalAssets.style.transition = `width ${delay.toFixed(0)}ms`;
            finalAssetsLabel.style.transition = `transform ${delay.toFixed(0)}ms`;
            setTimeout(stepValue, Math.round(delay));
          } else {
            svgRoot.classList.remove('running-remaining-simulations');
          }
        }
        stepValue();
        svgRoot.classList.add('running-remaining-simulations');
      }
    })
  
  fromEvent(finalizeButton, "click")
    .subscribe(() => {
      if (window.confirm("Sie haben ihre Aufteilung final festgelegt? Nach der Besttigung knnen Sie Ihre Aufteilung nicht mehr ndern.")) {
        window.alert("Vielen Dank fr Ihre Teilnahme! Der Versuch ist beendet.");
        confetti({ disableForReducedMotion: true});
      }
    })
  }  
