import { type ReferenceElement, computePosition, flip, shift, arrow, hide, autoUpdate, type Placement } from "@floating-ui/dom";
import { Observable, of } from "rxjs";

export type MaybeObservable<T> = T | Observable<T>;

interface AddOverlayArguments {
  target: ReferenceElement;
  text: MaybeObservable<string>;
  placement?: Placement;
  fallbackPlacements?: Placement[];
  styles?: MaybeObservable<Partial<CSSStyleDeclaration>>;
  hideIf?: Observable<boolean>;
  button?: {
    text: string;
    onClick: () => void;
  }
}

export async function addOverlay({ target, text, placement = "right", hideIf, styles, button, fallbackPlacements }: AddOverlayArguments) {
  let enforcedHide = false;

  const anchorElement = document.createElement('div');
  anchorElement.classList.add('arrow');

  const tooltipElement = document.createElement('div');
  tooltipElement.classList.add('tooltip');
  tooltipElement.textContent = 'Loading...';
  tooltipElement.appendChild(anchorElement);
  
  let buttonElement: HTMLButtonElement | null = null;
  if (button) {
    buttonElement = document.createElement('button');
    buttonElement.classList.add('button');
    buttonElement.textContent = button.text;
    buttonElement.onclick = button.onClick;
    tooltipElement.appendChild(buttonElement);
  }

  document.body.appendChild(tooltipElement);

  if (hideIf) {
    hideIf.subscribe(hidden => {
      enforcedHide = hidden;
      //tooltipElement.style.opacity = hidden ? '0' : '1';
      tooltipElement.style.visibility = hidden ? 'hidden' : 'visible';
    });
  }

  async function updatePosition() {
    const { x, y, middlewareData: { arrow: arrowPosition, hide: hideInfos }, placement: finalPlacement } = await computePosition(target, tooltipElement, {
      placement,
      middleware: [
        flip({ fallbackPlacements }),
        shift({ padding: 4 }),
        arrow({
          element: anchorElement,
          padding: 4 /* stop 5 px before the edge */
        }),
        hide()
      ]
    });

    Object.assign(tooltipElement.style, {
      marginLeft: finalPlacement === 'right' ? `${anchorElement.offsetWidth / 2}px` : 0,
      marginRight: finalPlacement === 'left' ? `${anchorElement.offsetWidth / 2}px` : 0,
      marginTop: finalPlacement === 'bottom' ? `${anchorElement.offsetWidth / 2}px` : 0,
      marginBottom: finalPlacement === 'top' ? `${anchorElement.offsetWidth / 2}px` : 0,
      left: finalPlacement === 'left' ? `${x - 8}px` : `${x}px`,
      top: `${y}px`,
      //opacity: (hideInfos?.referenceHidden || enforcedHide) ? 0.0 : 1.0,
      visibility: (hideInfos?.referenceHidden || enforcedHide) ? 'hidden' : 'visible'
    });

    if (anchorElement && arrowPosition) {
      anchorElement.classList.remove('left', 'right', 'top', 'bottom');
      anchorElement.classList.add(finalPlacement);
      Object.assign(anchorElement.style, {
        left: finalPlacement === 'right' ? `-${anchorElement.offsetWidth}px` : arrowPosition.x != null ? `${arrowPosition.x}px` : '',
        right: finalPlacement === 'left' ? `-${anchorElement.offsetWidth}px` : '',
        top: finalPlacement === 'bottom' ? `-${anchorElement.offsetWidth}px` : arrowPosition.y != null ? `${arrowPosition.y}px` : '',
        bottom: finalPlacement === 'top' ? `-${anchorElement.offsetWidth}px` : '',
      });
    }
  }

  if (typeof text === 'string')
    text = of(text);
  if (!(styles instanceof Observable) && typeof styles === 'object') {
    console.log('style is css style declaration', styles)
    styles = of(styles)
  }
  if (styles instanceof Observable) 
    styles.subscribe(nextStyles => {
      console.log('nextStyles', nextStyles)
      Object.assign(tooltipElement.style, nextStyles)
    });

  text.subscribe(nextText => {
    tooltipElement.textContent = nextText;
    tooltipElement.appendChild(anchorElement);
    if (buttonElement) tooltipElement.appendChild(buttonElement);
    updatePosition();
  });

  await updatePosition();

  const cleanup = autoUpdate(target, tooltipElement, updatePosition);

  function destroy() {
    tooltipElement.remove();
    cleanup();
  }

  return { updatePosition, destroy, tooltipElement, anchorElement };
}
